import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';
import { Row, Button, Col, FormGroup, Input, Table, Label } from 'reactstrap';
import { rgbToHex } from '@coreui/coreui/dist/js/coreui-utilities';
import jsPDF from "jspdf";
import "jspdf-autotable";
import logo from '../../assets/img/brand/mispace3.png'
import './Reports.scss'
import Server_connections from '../../constants/constants';

class ThemeView extends Component {
  constructor(props) {
    super(props);

    this.state = {
      bgColor: 'rgb(255, 255, 255)'
    }
  }

  componentDidMount () {
    const elem = ReactDOM.findDOMNode(this).parentNode.firstChild
    const color = window.getComputedStyle(elem).getPropertyValue('background-color')
    this.setState({
      bgColor: color || this.state.bgColor
    })
  }

  render() {

    return (
      <table className="w-100">
        <tbody>
        <tr>
          <td className="text-muted">HEX:</td>
          <td className="font-weight-bold">{ rgbToHex(this.state.bgColor) }</td>
        </tr>
        <tr>
          <td className="text-muted">RGB:</td>
          <td className="font-weight-bold">{ this.state.bgColor }</td>
        </tr>
        </tbody>
      </table>
    )
  }
}

class ThemeColor extends Component {
  render() {

    // const { className, children, ...attributes } = this.props
    const { className, children } = this.props

    const classes = classNames(className, 'theme-color w-75 rounded mb-3')

    return (
      <Col xl="2" md="4" sm="6" xs="12" className="mb-4">
        <div className={classes} style={{paddingTop: '75%'}}></div>
        {children}
        <ThemeView/>
      </Col>
    )
  }
}

class GeneralAnnuallyReport extends Component {
  constructor(props) {
    super(props);
    this.state = {
      years: [],
      months: [],
      bookings: [],
      isLoaded: false,
      searchItem: '',
      icon:logo
    }
    this.handleExcelExport = this.handleExcelExport.bind(this);
    this.handleLoadButton = this.handleLoadButton.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  onChange(e){
    this.setState({[e.target.name]: e.target.value});
  }

  handleExcelExport(e){
    e.preventDefault();

    var doc = new jsPDF({
        unit: 'in',
         format: 'a4' 
    })

    const title = "General Annual Bookings Report";
    const headers = [["Month", "Year", "Landlord Ref", "Amount", "Added at"]];

    const data = this.state.bookings.map(elt => [elt.month, elt.year, elt.landlord_code, elt.payment, elt.created_at]);

    let content = {
      startY: 2.0,
      head: headers,
      body: data,
    };

    doc.addImage(this.state.icon, 'png', 2.8, 0.5, 2.2, 0.5)
    doc.setFontSize(14)
    doc.text(title, 2.6, 1.4)
    doc.text(`Year: ${this.state.year}`, 0.5, 1.7).setFontSize(5)
    doc.autoTable(content);

    doc.save(`${this.state.year}.pdf`)
  }

  handleSearchInput = (e) => {
    this.setState({ searchItem: e.target.value})
  }

  handleLoadButton(e){
    e.preventDefault();

    fetch(Server_connections.GET_SPECIFIC_BOOKINGS_URL + `/${this.state.year}`)
    .then(res => res.json())
    .then(json => {
      this.setState({
        bookings:  json.bookings,
        isLoaded: true
      })
    })
  }

  componentDidMount(){
    fetch(Server_connections.GET_MONTHS_URL)
    .then(res => res.json())
    .then(json => {
      this.setState({
        months:  json.months,
        isLoaded: true
      })
    });

    fetch(Server_connections.GET_YEARS_URL)
    .then(res => res.json())
    .then(json => {
      this.setState({
        years:  json.years,
        isLoaded: true
      })
    });
  }

  render() {
    var { months, years, bookings } = this.state;
    let filteredItem = bookings.filter((item) => {
        return item.landlord_code.toLowerCase().includes(this.state.searchItem.toLowerCase());
    });

    return (
      <div className="animated fadeIn">
        <div className="card">
          <div className="card-header">
            <FormGroup row className="my-0">
                <Col xs="2">
                    <FormGroup>
                        <Input type="select" name="year" value={this.state.year} onChange={this.onChange}>
                            <option>select a year</option>
                            <option></option>
                            {years.map((y, index) => (
                            <option key = {index}>{y.year}</option>
                            ))}
                        </Input>
                    </FormGroup>
                </Col>
                <Col xs="2">
                    <FormGroup>
                        <Button className="btn-pill btn-success" size="sm" onClick = {this.handleLoadButton}>
                            <strong>Load</strong>
                        </Button>
                    </FormGroup>   
                </Col>
                <Col xs="4">
                    <FormGroup>
                    </FormGroup>
                </Col>
                <Col xs="2">
                    <FormGroup>
                    </FormGroup>
                </Col>
                <Col xs="2">
                    <Button className="btn-pill btn-success" size="sm" onClick = {this.handleExcelExport}>
                        <strong>Download PDF Report</strong>
                    </Button>
                </Col>
            </FormGroup>
          </div>
          <div className="card-body">
          <div className="parentDiv">
            <Table hover responsive className="table-outline mb-0 d-none d-sm-table">
                <thead className="thead-light">
                <tr>
                  <th>Year</th>
                  <th>Month</th>
                  <th>Landlord Ref</th>
                  <th>Amount </th>
                  <th>Added at</th>
                </tr>
                </thead>
                <tbody>
                {bookings.map((item, index) => (
                <tr key={ index }>
                  <td>
                    <div>{item.year}</div>
                  </td>
                  <td>
                    <div>{item.month}</div>
                  </td>
                  <td className="">
                    <div>{item.landlord_code}</div>
                  </td>
                  <td>
                    <div>{item.payment}</div>
                  </td>
                  <td>
                    <div>{item.created_at}</div>
                  </td>
                </tr>
                ))}
                </tbody>
              </Table>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default GeneralAnnuallyReport;
